.spinner {
	width: 44px;
	height: 44px;
	background-color: #333;
	display: block;

	border-radius: 100%;
	animation: sk-scaleout 1.0s infinite ease-in-out;
  }

  @keyframes sk-scaleout {
	0% {
	  transform: scale(0);
	} 100% {
	  transform: scale(1.0);
	  opacity: 0;
	}
  }



